"use client";
import { useRouter } from "next/router";
import { useEffect, useState } from "react";
import ReactMarkdown from "react-markdown";

export default function TopicPage() {
  const router = useRouter();
  const { subject, topic } = router.query;

  const [topics, setTopics] = useState<{ [key: string]: string }>({});
  const [content, setContent] = useState("");
  const [error, setError] = useState("");

  const cacheKey = `md-cache-${subject}`;

  useEffect(() => {
    if (!subject || !topic) return;

    const cached = localStorage.getItem(cacheKey);
    if (!cached) {
      setError("Open subject page first");
      return;
    }

    const parsed = JSON.parse(cached);
    setTopics(parsed);

    if (parsed[topic as string]) {
      setContent(parsed[topic as string]);
    } else {
      setError("Topic not found");
    }
  }, [subject, topic]);

  if (error) {
    return <div style={{ padding: 40, color: "red" }}>{error}</div>;
  }

  return (
    <div
      style={{
        minHeight: "100vh",
        display: "flex",
        flexDirection: "column",
        fontFamily: "Segoe UI, sans-serif",
        background: "#ecfdf5",
      }}
    >
      {/* HEADER */}
      <header
        style={{
          height: 64,
          display: "flex",
          alignItems: "center",
          padding: "0 30px",
          background: "linear-gradient(90deg, #22c55e, #3b82f6)",
          color: "#fff",
          fontSize: 22,
          fontWeight: 600,
          gap: 12,
          boxShadow: "0 4px 10px rgba(0,0,0,0.15)",
        }}
      >
        <img
          src="/favicon_new.png"
          alt="logo"
          style={{ width: 34, height: 34 }}
        />
        <span>{subject || "Learning Portal"}</span>
      </header>

      {/* BODY */}
      <div style={{ display: "flex", flex: 1 }}>
        {/* LEFT SIDEBAR */}
        <aside
          style={{
            width: 260,
            background: "#ffffff",
            borderRight: "1px solid #bbf7d0",
            padding: "20px 16px",
            position: "sticky",
            top: 64,
            height: "calc(100vh - 64px)",
            overflowY: "auto",
          }}
        >
          <h3
            style={{
              marginBottom: 16,
              color: "#065f46",
              fontSize: 18,
              fontWeight: 600,
            }}
          >
            Topics
          </h3>

          {Object.keys(topics).map((t, i) => (
            <a
              key={t}
              href={`/topic/${encodeURIComponent(t)}?subject=${encodeURIComponent(
                subject as string
              )}`}
              style={{
                display: "block",
                padding: "10px 12px",
                marginBottom: 6,
                borderRadius: 6,
                textDecoration: "none",
                fontWeight: 500,
                color: t === topic ? "#fff" : "#065f46",
                background:
                  t === topic
                    ? "linear-gradient(90deg, #22c55e, #3b82f6)"
                    : "transparent",
                transition: "0.2s",
              }}
            >
              {i + 1}. {t}
            </a>
          ))}
        </aside>

        {/* RIGHT CONTENT */}
        <main
          style={{
            flex: 1,
            padding: "30px 40px",
            background: "#ffffff",
            overflowX: "auto",
          }}
        >
          <h1
            style={{
              fontSize: 28,
              marginBottom: 20,
              color: "#065f46",
              borderBottom: "2px solid #a7f3d0",
              paddingBottom: 8,
            }}
          >
            {topic}
          </h1>

          {content ? (
            <ReactMarkdown>{content}</ReactMarkdown>
          ) : (
            <p>Loading...</p>
          )}
        </main>
      </div>

      {/* FOOTER */}
      <footer
        style={{
          height: 44,
          background: "linear-gradient(90deg, #22c55e, #3b82f6)",
          color: "#fff",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          fontSize: 14,
          fontWeight: 500,
        }}
      >
        Â© 2026 Learning Portal
      </footer>
    </div>
  );
}
